																			{{ subject.subject_name }} 
																		</div>
																		<div class="subject-professor">
																			<b-form-select v-model="subject.professor" :options="subject.professors"></b-form-select>
																		</div>
																		<div class="subject-days">
																			<div class="subject-days-wrapper values">
																				<div class="day-info" v-for="(day, id) in weekdays" :key="day.id">
																					<b-form-input type="number" placeholder="Lectures" v-model="subject.days[id]" min="0"></b-form-input>
																				</div>
																			</div>
																		</div>
																	</div>
																</template>
															</div>
														</div>
