  	<!-- Select Subject Modal -->
    <b-modal ref="subjects-modal" hide-footer :title="$ml.get('options')">
        <b-form-group label="Subject type">
			<b-form-radio-group id="subject-type-selector" buttons v-model="subject_type" name="subject_type" @input="handleSubjectTypeChange">
				<b-form-radio value="subject">Subject</b-form-radio>
				<b-form-radio value="subject_group">Subject group</b-form-radio>
			</b-form-radio-group>
		</b-form-group>
		<b-form-group v-if="subject_type !== ''">
			<label>Select subject:</label>
			<treeselect v-model="selectedSubject" v-if="subject_type == 'subject'" :options="subjects_tree" :default-expand-level="10" :placeholder="$ml.get('select_subject')"></treeselect>
			<treeselect v-model="selectedSubject" v-if="subject_type == 'subject_group'" :options="subjects_flat" :default-expand-level="10" :placeholder="$ml.get('select_subject')"></treeselect>
		</b-form-group>
		<br>
      <b-button variant="primary" @click="addSubject">Add</b-button>
    </b-modal>
  </div>
</template>
	
<script>

	//Imports
	import {acadUrl} from '@/apiUrl'
	import apiUrl from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import { ToolbarPlugin } from "@syncfusion/ej2-vue-navigations"
	import Treeselect from '@riophae/vue-treeselect'
	import '@riophae/vue-treeselect/dist/vue-treeselect.css'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr'
	import draggable from 'vuedraggable'
	
	Vue.use(ToolbarPlugin)
	
	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	let termID    = 1;
	let sectionID = 1;
	let sectionTitle = 'A';
	let groupID   = 0;

	let validFrom = new Date().getFullYear()
	let validTo   = validFrom + 1

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)

    //Vue Handler
	export default {
		name:"ProgrammeDetails",
		components:{
			Treeselect,
			draggable
		},
		display: "Handle",
		data : function () {
			return {
				thisProgramme: undefined,
				//form data
				saveTemplate: function () {return {template: Vue.component("addTemplate", {template: `<b-badge id="label1" variant="success" ><i class="fa fa-save"></i>&nbsp<span v-text="$ml.get('save')"></span></b-badge>`, data() {return {data: {}, }; }, }) } },
				professors: [],
				programme : {
					programme_name: '',
					programme_department: null,
					programme_credits: 0,
					programme_duration: '',
					valid_from: validFrom,
					valid_to: validTo
				},
				createdProgramme: false,
				edit_pic:false,
				copyAddress:false,
				profile_pic:"",
				//Programme List
				programmes : [{name:"No Programmes", _id:"0", disabled:true}],
				//Academic-Term List
				academic_terms : [{
					id: 0,
					academic_term_name: `Academic term ${termID}`,
					months: 0,
					start_month: '',
					subjects: [],
					subjectsInfo: [],
					additional_subjects_group: [],
					showAdditional: false,
					collapsed: false
				}],
				//Department List
				departments:[],
				dept:[],
				subjects:[],
				subjects_tree:[],
				subjects_flat:[],
				selectedSubject: null,
				add_new_row: false,
				subject_type: '',
				academic_subjects: [],
				current_term: null,
				current_group: null,
				additionalSubjectsGroup: [],
				aSAside: false,
				numberOfSubjectsOptions: [
					{
						value: 0,
						text: 'Exact'
					},
					{
						value: 1,
						text: 'Min-max'
					}
				],
				programme_sections: [
					{
						id: 0,
						value: 0,
						section_name: `${sectionTitle}`,
						text: `${sectionTitle}`,
						max_strength: 50,
						class_representative: '',
						collapsed: false
					}
				],
				weekdays: [
					{
						text: 'Sunday',
						value: 0
					},
					{
						text: 'Monday',
						value: 1
					},
					{
						text: 'Tuesday',
						value: 2
					},
					{
						text: 'Wednesday',
						value: 3
					},
					{
						text: 'Thursday',
						value: 4
					},
					{
						text: 'Friday',
						value: 5
					},
					{
						text: 'Saturday',
						value: 6
					}
				],
				months: [
					{
						text: 'January',
						value: 1
					},
					{
						text: 'February',
						value: 2
					},
					{
						text: 'March',
						value: 3
					},
					{
						text: 'April',
						value: 4
					},
					{
						text: 'May',
						value: 5
					},
					{
						text: 'June',
						value: 6
					},
					{
						text: 'July',
						value: 7
					},
					{
						text: 'August',
						value: 8
					},
					{
						text: 'September',
						value: 9
					},
					{
						text: 'October',
						value: 10
					},
					{
						text: 'November',
						value: 11
					},
					{
						text: 'December',
						value: 12
					}
				]
			}
		},
		methods:{
			addAcademicTerms() {
				this.academic_terms.push({
					id: termID++,
					academic_term_name: `Academic term ${termID}`,
					months: 0,
					start_month: '',
					subjects: [],
					subjectsInfo: [],
					additional_subjects_group: [],
					showAdditional: false,
					collapsed: false
				})
			},
			copyAcademicTerm(id) {
				const academicTerms = JSON.parse(JSON.stringify(this.academic_terms))
				const newTerm       = academicTerms.filter(term => term.id == id)
				newTerm[0].id       = termID++
				newTerm[0].academic_term_name    = `Academic term ${termID}`

				this.academic_terms.push(newTerm[0])
			},
			removeAcademicTerms(id) {
				this.academic_terms = this.academic_terms.filter(term => id != term.id )
			},
			showSubjectModal(id, type = 'term') {
				 if (type == 'term') {
					this.current_term = id
				} else if (type == 'additional') {
					this.current_group = id
				}
				this.currentAddType = type
				this.selectedSubject = null
				this.subject_type = ''
				this.$refs['subjects-modal'].show()
			},
			handleSubjectTypeChange() {
				this.selectedSubject = null
			},
			hideSubjectModal() {
				this.$refs['subjects-modal'].hide()
			},
			onDelete(j) {
				this.academic_subjects.splice(j, 1);
			},
			only_parent(lists) {
				return lists.filter(list => {
					if(list.parent_subject == null){
						return true;
					} else {
						return false;
					}
				});
			},
			addSubject() {
				if(this.selectedSubject) {
					this.hideSubjectModal()

					if (this.currentAddType == 'term') {
						this.academic_terms = this.handleAddSubject()
					} else if (this.currentAddType == 'additional') {
						this.additionalSubjectsGroup = this.handleAddSubject()
					}
					this.selectedSubject = null;
				} else {
					if (this.subject_type == 'additional_group') {
						this.academic_terms.forEach(term => {
							if (term.id == this.current_term) term.showAdditional = true
						})
						this.hideSubjectModal()
					}
				}
			},
			handleAddSubject() {
				let data = [], checkId;
				if (this.currentAddType == 'term') {
					data = this.academic_terms
					checkId = this.current_term
				} else if (this.currentAddType == 'additional') {
					data = this.additionalSubjectsGroup
					checkId = this.current_group
				}
				let subjectInfo = {}

				subjectInfo = this.subjects.filter(subject => subject._id == this.selectedSubject)
				subjectInfo = subjectInfo[0]

				this.$set(subjectInfo, 'days', [0, 0, 0, 0, 0, 0, 0])
				this.$set(subjectInfo, 'professor', '')
				this.$set(subjectInfo, 'professors', [])
				this.$set(subjectInfo, 'total_lectures', 0)
				this.$set(subjectInfo, 'time_frame', 0)
				this.$set(subjectInfo, 'allSections', true)
				this.$set(subjectInfo, 'sections', [])
				this.$set(subjectInfo, 'subject_type', this.subject_type)

				data.forEach((term) => {
					if (term.id === checkId) {
						if (!this.checkDuplicateSubject(term.subjects, this.selectedSubject)) {
							term.subjects.push(this.selectedSubject)
							term.subjectsInfo.push(subjectInfo)
						} else {
							toast({
								type: VueNotifications.types.error,
								title: 'Subject(s) already exists in this term!!'
							})
						}
						if (this.subject_type == 'subject_group') {
							if (subjectInfo.hasOwnProperty('children'))
							{
								subjectInfo.children.forEach(node => {
									if (!this.checkDuplicateSubject(term.subjects, node._id)) {
										let thisSubjectsInfo = this.subjects.filter(subject => subject._id == node._id)
										thisSubjectsInfo = thisSubjectsInfo[0]

										this.$set(thisSubjectsInfo, 'days', [0, 0, 0, 0, 0, 0, 0])
										this.$set(thisSubjectsInfo, 'professor', '')
										this.$set(thisSubjectsInfo, 'professors', [])
										this.$set(thisSubjectsInfo, 'total_lectures', 0)
										this.$set(thisSubjectsInfo, 'time_frame', 0)
										this.$set(thisSubjectsInfo, 'allSections', true)
										this.$set(thisSubjectsInfo, 'sections', [])

										term.subjects.push(node._id)
										term.subjectsInfo.push(thisSubjectsInfo)
									} else {
										toast({
											type: VueNotifications.types.error,
											title: 'Subject(s) already exists in this term!!'
										})
									}
								})
							} else {

							}
						}
					}
				});

				return data;
			},
			removeSubject(termId, subject) {
				this.academic_terms.forEach((term) => {
					if (term.id === termId) {
						term.subjects = term.subjects.filter(sub => sub != subject)
						term.subjectsInfo = term.subjectsInfo.filter(info => info._id != subject)
					}
				})
			},
			checkDuplicateSubject(subjects, subject) {
				if (subjects.indexOf(subject) < 0) {
					return false
				}
				return true
			},
			manageSubjectGroups(term, group) {
				this.aSAside = true
				this.current_term = term.id
				// const group = term.additionalSubjects.filter(sub => sub.id == group.id)
				this.additionalSubjectsGroup = term.additional_subjects_group.filter(sub => sub.id == group.id)
			},
			addAdditionalSubjectGroup(term) {
				this.aSAside = true
				this.current_term = term.id

				this.additionalSubjectsGroup.push({
					id: groupID++,
					title: `Subject group ${groupID}`,
					min_subjects_group: 1,
					min_credits: 1,
					minSubjects: 1,
					maxSubjects: 1,
					subjects : [],
					subjectsInfo: [],
					numberOfSubjectsValue: 0
				})
			},
			addSubjectToGroup(group) {
				this.showSubjectModal(group, 'additional')
			},
			removeSubjectGroup(term, group) {
				this.academic_terms.forEach(aterm => {
					if (term.id == aterm.id) {
						aterm.additionalSubjects = aterm.additionalSubjects.filter(asub => asub.id != group.id)
					}
				})
			},
			removeSubjectFromGroup(group, subject) {
				this.additionalSubjectsGroup.forEach(subgroup => {
					if (subgroup.id == group) {
						subgroup.subjectsInfo = subgroup.subjectsInfo.filter(sub => sub._id != subject)
						subgroup.subjects = subgroup.subjects.filter(sub => sub != subject)
					}
				})
			},
			saveAdditionalSubjectsGroup() {
				let editMode = false;
				this.academic_terms.forEach(term => {
					if (term.id == this.current_term) {
						term.additional_subjects_group.forEach(group => {
							if (group.id == this.additionalSubjectsGroup[0].id) {
								group = this.additionalSubjectsGroup
								editMode = true
							}
						})
						if (!editMode) {
							term.additional_subjects_group = term.additional_subjects_group.concat(this.additionalSubjectsGroup)
						}
					}
				})
				this.closeAsAside();
			},
			closeAsAside() {
				this.additionalSubjectsGroup = []
				this.current_term = null
				this.aSAside = false
			},
			addSection() {
				sectionTitle = String.fromCharCode(sectionTitle.charCodeAt() + 1)
				this.programme_sections.push({
					id: sectionID,
					value: sectionID++,
					section_name: sectionTitle,
					text: sectionTitle,
					max_strength: 50,
					class_representative: '',
					subjects: [],
					collapsed: false
				})
			},
			removeSection(id) {
				this.programme_sections = this.programme_sections.filter(section => section.id !== id)
			},
			list_to_tree_dept(list) {
	          var map = {}, node, roots = [], i;
	          for (i = 0; i < list.length; i += 1) {
	              map[list[i]._id] = i; // initialize the map
	              list[i].children = []; // initialize the children
	          }
	          for (i = 0; i < list.length; i += 1) {
	              node = list[i];
	              if (node.parent_department != undefined && this.checkExist(node.parent_department)) {
	                  // if you have dangling branches check that map[node.parentId] exists
	                  list[map[node.parent_department]].children.push(node);
	              } else {
	                  roots.push(node);
	              }
	          }
	          this.convertDataDept(roots)
	          return roots
			},
			convertDataDept(roots) {
				for(var i=0;i<roots.length;i++) {
					if(roots[i].children.length !=0) {
						this.convertDataDept(roots[i].children);
					}
					roots[i].id = roots[i]._id;
					roots[i].label = roots[i].department_name;
					if(roots[i].children.length<=0) {
						delete roots[i].children
					}
				}
			},
			checkExist(node) {
				for (var i=0; i < this.dept.length; i++) {
					if (this.dept[i]._id == node)
						return true;
				}
				return false;
			},
			completeProgrammeCreation(args) {

			},
			//Convert Flat Data to Tree Data
			list_to_tree_subj(list, selectableChildren = true) {
				var map = {}, node, roots = [], i;
				for (i = 0; i < list.length; i += 1) {
					map[list[i]._id] = i; // initialize the map
					list[i].children = []; // initialize the children
				}
				for (i = 0; i < list.length; i += 1) {
					node = list[i];
					if (node.parent_subject != undefined && this.checkExistSubj(node.parent_subject)) {
						// if you have dangling branches check that map[node.parentId] exists
						if (!selectableChildren) node.isDisabled = true
						list[map[node.parent_subject]].children.push(node);
					} else {
						roots.push(node);
					}
				}
				this.convertDataSubj(roots)
				return roots
			},
			// Check if Parent Subject exists
			checkExistSubj(node) {
				for (var i=0; i < this.subjects.length; i++) {
					if (this.subjects[i]._id == node)
						return true;
				}
				return false;
			},
			// Convert Data for Treeselect
			convertDataSubj(roots) {
				for(var i=0;i<roots.length;i++) {
					if(roots[i].children.length !=0) {
					this.convertDataSubj(roots[i].children);
					}
					roots[i].id = roots[i]._id;
					roots[i].label = roots[i].subject_name;
					if(roots[i].children.length<=0) {
					delete roots[i].children
					}
				}
			},
			on_add_row_click(){
				this.add_new_row=true;
				alert('on_add_row_click');
			},
			getFilteredProfessors(subjects) {
				subjects.forEach(sub => {
					this.professors.forEach(prof => {
						prof.substitution_details.forEach(subD => {
							if (subD.subject == sub._id) {
								let exists = false
								sub.professors.forEach(sProf => {
									if (sProf._id == prof._id) exists = true
								})
								if (!exists) sub.professors.push(prof)
							}
						})
					})
				})
				return subjects
			},
			filterSubjectsBySections() {
				this.programme_sections.forEach(section => {
					if (!section.hasOwnProperty('terms')) {
						section.terms = JSON.parse(JSON.stringify(this.academic_terms))
					}
					
					section.terms.forEach(term => {
						term.subjectsInfo = term.subjectsInfo.filter(subject => {
							if (!subject.allSections) return subject.sections.includes(section.id)
							else return true
						})
						term.additional_subjects_group.forEach(group => {
							group.subjectsInfo = group.subjectsInfo.filter(subject => {
								if (!subject.allSections) return subject.sections.includes(section.id)
								else return true
							})
						}) 
					})
				})
			},
			calculateCredits() {
				let credits = 0

				this.programme_sections.forEach(section => {
					if (section.hasOwnProperty('terms')) {
						section.terms.forEach(term => {
							term.subjectsInfo.forEach(subject => {
								credits += parseInt(subject.subject_credits)
							})
							term.additional_subjects_group.forEach(group => {
								credits += parseInt(group.min_credits)
							})
						})
					}
				})

				return credits
			},
			submitProgramme() {
				if (this.programme.programme_name != '') {
					if (this.thisProgramme == undefined) {						
						api.post(`${acadUrl}prog/programme/create`, this.programme).then(prog => {
							if(prog.data._id) {
								this.createdProgramme = true
								this.submitAcademicTerms(prog.data._id)
								// this.$router.push()
							}
							else {
								toast({
									type: VueNotifications.types.error,
									title: 'Something Went Wrong !!'
								})
							}
						})
					}
					else {
						api.put(`${acadUrl}prog/programme/update/one/${this.thisProgramme}`, this.programme).then(prog => {
							this.submitAcademicTerms(this.thisProgramme)
						})
						.catch(err => {
							toast({
								type: VueNotifications.types.error,
								title: err
							})
						})
					}
				} else {
					toast({
						type: VueNotifications.types.error,
						title: 'Please enter program name first!'
					})
				}
			},
			submitAcademicTerms(programmeId) {
				let academic_terms = JSON.parse(JSON.stringify(this.academic_terms))
				academic_terms.forEach(term => {
					term = this.prepareTerm(term, programmeId)
					if (term.hasOwnProperty('_id')) {
						api.put(`${acadUrl}acad/term/update/one/${term._id}`).then(response => {
							this.submitSections(programmeId, term._id, term.id)
						})
						.catch(err => {
							toast({
								type: VueNotifications.types.error,
								title: err
							})
						})
					}
					else {
						api.post(`${acadUrl}acad/term/create`, term).then(response => {
							if (response.data._id) {
								this.submitSections(programmeId, response.data._id, term.id)
							} else {
								toast({
									type: VueNotifications.types.error,
									title: `Something Went Wrong !! Cannot create term: ${term.academic_term_name}`
								})
							}
						})
					}
				})
			},
			prepareTerm(term, programmeId) {
				term.programme = programmeId
				let subjects = []

				term.subjectsInfo.forEach(sub => {
					subjects.push({
						subject_id: sub._id,
						credit: sub.subject_credits,
						subject_type: sub.subject_type,
						total_lectures: sub.total_lectures,
						time_frame: sub.time_frame
					})
				})

				let groups = []

				term.additional_subjects_group.forEach(group => {
					let addSubjects = []
					group.subjectsInfo.forEach(sub => {
						addSubjects.push({
							subject_id: sub._id,
							credit: sub.subject_credits,
							subject_type: sub.subject_type,
							total_lectures: sub.total_lectures,
							time_frame: sub.time_frame
						})
					})

					groups.push({
						group_name: group.title,
						min_credits: group.min_credits,
						min_subjects_group: group.min_subjects_group,
						min_subjects: group.minSubjects,
						max_subjects: group.maxSubjects,
						subjects: addSubjects
					})
				})

				delete term.subjects
				delete term.collapsed
				delete term.subjectsInfo
				delete term.showAdditional

				Object.assign(term, {
					subjects: subjects,
					additional_subjects_group: groups
				})

				return term
			},
			submitSections(programmeId, termId, thisTermId) {
				let sections = JSON.parse(JSON.stringify(this.programme_sections))

				sections.forEach(section => {
					section = this.prepareSection(section, programmeId, termId)
					if (section.hasOwnProperty('_id')) {

					}
					else {
						api.post(`${acadUrl}sec/section/create`, section).then(response => {
							if (response.data._id) {
								this.submitTimeTablePlanner(programmeId, termId, response.data._id, section.id, thisTermId)
							} else {
								toast({
									type: VueNotifications.types.error,
									title: `Something Went Wrong !! Cannot create section: ${section.section_name}`
								})
							}
						})
					}
				})
			},
			prepareSection(section, programmeId, termId) {
				section.programme = programmeId
				section.academic_term = termId

				delete section.collapsed
				delete section.text
				delete section.value
				delete section.terms

				return section
			},
			submitTimeTablePlanner(programmeId, termId, sectionId, thisSectionId, thisTermId) {
				let sections = JSON.parse(JSON.stringify(this.programme_sections))

				sections.forEach(section => {
					let timeTable = {}
					if (section.id == thisSectionId) {
						timeTable = this.prepareTimeTablePlanner(section, programmeId, termId, sectionId, thisTermId)
						if (timeTable.hasOwnProperty('_id')) {
							api.put(`${acadUrl}ttplan/timetableplanner/update/one/${timeTable._id}`, timeTable).then(response => {
								toast({
									type: VueNotifications.types.success,
									title: `Updated successfully!`
								})
							})
						}
						else {
							api.post(`${acadUrl}ttplan/timetableplanner/create`, timeTable).then(response => {
								if (response.data._id) {
									toast({
										type: VueNotifications.types.success,
										title: `Created successfully!`
									})
								} else {
									toast({
										type: VueNotifications.types.error,
										title: `Something Went Wrong !! Cannot create timetable for ${section.section_name}`
									})
								}
							})
						}
					}
				})

			},
			prepareTimeTablePlanner(section, programmeId, termId, sectionId, thisTermId) {
				let timeTable = {}
				let subjects = []

				section.terms.forEach(term => {
					if (term.id == thisTermId) {
						term.subjectsInfo.forEach(sub => {
							subjects.push({
								subject: sub._id,
								professor: sub.professor == '' ? null : sub.professor,
								lectures: sub.days
							})
						})
					}
				})

				Object.assign(timeTable, {
					programme: programmeId,
					academic_term: termId,
					section: sectionId,
					subjects
				})

				return timeTable
			},
			fetchProgrammeData() {
				// Academic terms
				let tmp_terms            = []
				let tmp_sections         = []
				let tmp_timeTablePlanner = []
				api.get(`${acadUrl}acad/term/view/all/${this.thisProgramme}`).then(response => {
					tmp_terms = response.data

					api.get(`${acadUrl}sec/section/find/by/academic/${tmp_terms[0]._id}`).then(sections => {
						tmp_sections = sections.data

						tmp_sections.forEach(section => {
							section.id = section._id

							api.get(`${acadUrl}ttplan/timetableplanner/view/all`).then(timeTable => {
								tmp_timeTablePlanner = timeTable
							})
						})
						this.parseProgrammeData(tmp_terms, tmp_sections, tmp_timeTablePlanner)
					})
				})
			},
			parseProgrammeData(terms, sections, timetable) {
				terms.forEach(term => {
					this.$set(term, 'id', term._id)
					this.$set(term, 'subjectsInfo', term.subjects)
					this.$set(term, 'showAdditional', !(term.additional_subjects_group.length == 0))
					this.$set(term, 'collapsed', false)

					term.subjectsInfo.forEach(sub => {
						this.$set(sub, 'sections', [])
						this.$set(sub, 'allSections', true)
					})
				})

				sections.forEach(section => {
					this.$set(section, 'terms', terms)
				})
				
				this.academic_terms = terms

				this.programme_sections = sections
			},
		},
		async mounted(){

			this.thisProgramme = this.$route.params.progId

			if (this.thisProgramme !== undefined) {
				const programme = api.get(`${acadUrl}prog/programme/view/one/${this.thisProgramme}`).then(response => {
					this.programme = response.data
					this.fetchProgrammeData()
				})
			}

			api.get(`${apiUrl}department/dept/get`).then((departments) => {
				this.dept = JSON.parse(JSON.stringify(departments.data))
				this.departments = this.list_to_tree_dept(departments.data)
			})

			api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
				this.subjects = JSON.parse(JSON.stringify(subjects.data))
				this.subjects_tree = this.list_to_tree_subj(this.subjects, true)
				this.subjects_flat = this.list_to_tree_subj(subjects.data, false)
			})

			api.get(`${apiUrl}user/subuser/see/professors`).then(professors => {
				let profs = JSON.parse(JSON.stringify(professors.data))
				profs.forEach(prof => {
					prof.value = prof._id
					prof.text  = prof.user_name
				})
				this.professors = profs
			})

			// var apilink = apiUrl.split('/api')
			// console.log(this.$route.params)
			// await api.get(`${acadUrl}prog/programme/view/one/${this.$route.params.progId}`).then((programme) => {
			// 	this.programme = programme.data
			// })
		},
		computed: {
			
		},
		watch: {
			academic_terms: {
				handler() {
					this.academic_terms.forEach(term => {
						term.subjectsInfo = this.getFilteredProfessors(term.subjectsInfo)
						term.additional_subjects_group.forEach(group => {
							group.subjectsInfo = this.getFilteredProfessors(group.subjectsInfo)
						})
					})

					this.filterSubjectsBySections()
					this.programme.programme_credits = this.calculateCredits()
				},
				deep: true
			},
			programme_sections: {
				handler() {
					this.filterSubjectsBySections()
					this.programme.programme_credits = this.calculateCredits()
				},
				deep: true
			}
		}
	};
</script>

